import React from 'react'
import { useParams } from 'react-router-dom'
import { Breadcrumb, BreadCrumbPart, PageHeader, PageHeaderContent } from '@aserto/console-common'

import { Header } from './styles'

const HeaderComponent: React.FC = () => {
  const { id: topazInstance } = useParams()
  const breadcrumbParts: BreadCrumbPart[] = [
    { text: `Authorizers/${topazInstance}`, url: '/ui/authorizers' },
  ]

  return (
    <Header>
      <PageHeader hasBorderBottom={true}>
        <PageHeaderContent>
          <Breadcrumb breadcrumbParts={breadcrumbParts} />
        </PageHeaderContent>
      </PageHeader>
    </Header>
  )
}

export default HeaderComponent
